spacer {\n  flex: 1 1 auto;\n}\nhtml,\nbody {\n  height: 100%;\n}\n\nbody {\n  background-size: cover;\n}\n\n$quantity: 20;\n\nhtml,\nbody {\n  height: 100%;\n}\n\nbody {\n  background-size: cover;\n}\n\n.firefly {\n  position: fixed;\n  left: 50%;\n  top: 50%;\n  width: 0.5vw;\n  height: 0.4vw;\n  margin: (-0.2vw) 0 0 9.8vw;\n  animation: ease 200s alternate infinite;\n  pointer-events: none;\n\n  &::before,\n  &::after {\n    content: \"\";\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    border-radius: 50%;\n    transform-origin: -10vw;\n  }\n\n  &::before {\n    background: rgb(255, 222, 222);\n    opacity: 0.4;\n    animation: drift ease alternate infinite;\n  }\n\n  &::after {\n    background: white;\n    opacity: 0;\n    box-shadow: 0 0 0vw 0vw yellow;\n    animation: drift ease alternate infinite, flash ease infinite;\n  }\n}\n// Randomize Fireflies Motion\n@for $i from 1 through $quantity {\n  $steps: random(12) + 16;\n  $rotationSpeed: random(10) + 8s;\n\n  .firefly:nth-child(#{$i}) {\n    animation-name: move#{$i};\n\n    &::before {\n      animation-duration: #{$rotationSpeed};\n    }\n\n    &::after {\n      animation-duration: #{$rotationSpeed}, random(6000) + 5000ms;\n      animation-delay: 0ms, random(8000) + 500ms;\n    }\n  }\n\n  @keyframes move#{$i} {\n    @for $step from 0 through $steps {\n      #{$step * 100 / $steps}% {\n        transform: translateX(random(100) - 50vw)\n          translateY(random(100) - 50vh)\n          scale(random(75) / 100 + 0.25);\n      }\n    }\n  }\n}\n\n@keyframes drift {\n  0% {\n    transform: rotate(0deg);\n  }\n\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes flash {\n  0%,\n  30%,\n  100% {\n    opacity: 0;\n    box-shadow: 0 0 0vw 0vw rgb(0, 255, 55);\n  }\n\n  5% {\n    opacity: 1;\n    box-shadow: 0 0 2vw 0.4vw yellow;\n  }\n}\n$divs: 10;\n\n@for $i from 1 through $divs {\n  .rain:nth-child(#{$i}) {\n    position: fixed;\n    left: (2% + $i * $i);\n    top: 0%;\n    width: 0.6vw;\n    height: 0.5vw;\n    margin: (-0.2vw) 0 0 9.8vw;\n    animation: (0s + $i * 2) linear 0s infinite forwards rainDown;\n    pointer-events: none;\n\n    &::before,\n    &::after {\n      content: \"\";\n      position: absolute;\n      width: 1px;\n      background-color: white;\n      height: 5px;\n    }\n  }\n}\n\n@keyframes rainDown {\n  0% {\n    top: 0%;\n  }\n  100% {\n    top: 100%;\n  }\n}\n"],"sourceRoot":""}]);
